$fontPath: '../fonts/Montserrat/';

@font-face {
    font-family: 'Montserrat';
    src: url('#{$fontPath}Montserrat-Black.eot');
    src: url('#{$fontPath}Montserrat-Black.eot?#iefix') format('embedded-opentype');
    src: url('#{$fontPath}Montserrat-Black.woff2') format('woff2');
    src: url('#{$fontPath}Montserrat-Black.woff') format('woff');
    src: url('#{$fontPath}Montserrat-Black.ttf') format('truetype');
    font-weight: 900;
    font-style: normal;
    font-display: swap; }

